<template>
    <div id="app">
        <div id="nav">
            <div  class="container">
                <div class="row">
                    <div class="content">
                        <div class="title">
                            <div>NiceFish</div>
                        </div>
                        <div class="nav">
                            <router-link to="/">动态</router-link>
                            <router-link to="/about">集成图表</router-link>
                            <router-link to="/gaoDe">高德地图</router-link>
                            <router-link to="/liucheng">流程图</router-link>
                            <router-link to="/niceFish">NiceFish</router-link>
                            <router-link to="/codeBe">CodeBe</router-link>
                        </div>
                        <div class="right">
                            <!-- <router-link to="javascript:void(0)" @click="denglu">登录</router-link> -->
                            <a href="javascript:void(0)" @click="denglu">登录</a>
                            <router-link to="/zuCe">注册</router-link>
                        </div>
                    </div>
                </div>
            </div>     
        </div>
        <div>{{$t("m.title")}}</div>
        <div style="width:1300px;margin:auto">
            <!-- <router-view/> -->
            <transition :name="transitionName">   
                <router-view></router-view>
            </transition>
        </div>

  </div>
</template>



<script>
import './assets/css/app.css';

export default {
    name: 'App',
    data () {
        return {
            transitionName:''
        }
    },
    methods:{
        denglu : function(){
            this.$i18n.locale = 'en-US';
        }
    },
    watch: {
        $route(to, from) {
            if(to.meta.index > from.meta.index){
                //设置动画名称
                this.transitionName = 'slide-left';
            }else{
                this.transitionName = 'slide-right';
            }
        }
    }

}
</script>

<style>

.slide-right-enter-active,
.slide-right-leave-active,
.slide-left-enter-active,
.slide-left-leave-active {
  will-change: transform;
  transition: all 130ms;
  position: fixed;
}
.slide-right-enter {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
}
.slide-right-leave-active {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}
.slide-left-enter {
  opacity: 0;
  transform: translate3d(100%, 0, 0);
}
.slide-left-leave-active {
  opacity: 0;
  transform: translate3d(-100%, 0, 0);
}
</style>
